import { useNavigate } from "react-router-dom";
import "./Login.css";
import { Formik, ErrorMessage } from "formik";
import * as Yup from "yup";
import Swal from "sweetalert2";
import { useLocation } from "react-router";

function Login() {
  const location = useLocation();
  let userDb = location.state
  const navigate = useNavigate();

  console.log(userDb);




  const validationSchema = Yup.object().shape({
    email: Yup.string().email("   !").required(" !"),
    password: Yup.string().required(" !"),
  });
  const submit = ({email, password}) => {
    if(userDb.email === email && userDb.password === password) {
      Swal.fire({ title: " ", icon: "success" }).then(function () {
        navigate("/");
      });
    } else {
      Swal.fire({ title: " ", text: "   ", icon: "error" });
    }
  };

  return (
    <Formik
      initialValues={{
        email: "",
        password: "",
      }}
      validationSchema={validationSchema}
      onSubmit={submit}
    >
      {({ values, handleSubmit, handleChange }) => (
        <div className="login">
          <div className="loginDiv_1"></div>
          <form onSubmit={handleSubmit}>
            <div className="loginDiv_2">
              <div className="LogInput">
                <input type="text" value={values.email} name="email" placeholder=" " onChange={handleChange} className="LoginText" />
                <ErrorMessage name="email" />
              </div>
              <div className="LogInput">
                <input
                  type="password" value={values.password} name="password" placeholder=" " onChange={handleChange} className="LoginText"/>
                <ErrorMessage name="password" />
              </div>
            </div>
            <div className="registerDiv_3">
              <input value={""} type="submit" className="regBtn" />
              <input value={""} type="button" className="regBtn" />
            </div>
          </form>
        </div>
      )}
    </Formik>
  );
}

export default Login;
