import "./Register.css";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import * as yup from "yup";
import { Formik, ErrorMessage } from "formik";

function Register() {
  let navigate = useNavigate();

  const validationSchema = yup.object().shape({
    email: yup
      .string()
      .required(" ")
      .email("  ."),
    username: yup
      .string()
      .required(" ")
      .min(2, " 2  ")
      .max(8, " 8  ")
      .matches(/^[-a-zA-Z][^!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?\s]*$/, "      !"),
    password: yup
      .string()
      .required(",  8 .")
      .min(8, " 8  ")
      .max(15, " 15  ")
      .matches(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,15}$/, "   8 ."),
    passwordConfirm: yup.string().oneOf([yup.ref("password")], " ."),
  });

  const submit = async (values) => {
    const { email, password, username } = values;
    try {
      Swal.fire({ title: " ", icon: "success" }).then(function () {
        navigate("/login", {state : values});
      });
      console.log(values);
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <Formik
      initialValues={{
        email: "",
        username: "",
        password: "",
        passwordConfirm: "",
      }}
      validationSchema={validationSchema}
      onSubmit={submit}
    >
      {({ values, handleSubmit, handleChange, errors, touched }) => (
        <div className="register">
          <div className="registerDiv_1">
            <span className="star">*</span> 
          </div>
          <form onSubmit={handleSubmit}>
            <div className="registerSection">
              <div className="regTitle">
                <span className="star">*</span>
              </div>
              <div className="regInput">
                <input name="email" placeholder=" " value={values.email} onChange={handleChange} className="inputText" />
                {errors.email && <p className="errorMsg">{errors.email}</p>}
              </div>
            </div>
            <div className="registerSection">
              <div className="regTitle">
                <span className="star">*</span>
              </div>
              <div className="regInput">
                <input name="username" placeholder=" " value={values.username} onChange={handleChange} className="inputText" />
                {errors.username && <p className="errorMsg">{errors.username}</p>}
              </div>
            </div>
            <div className="registerSection">
              <div className="regTitle">
                <span className="star">*</span>
              </div>
              <div className="regInput">
                <input type="password" name="password" placeholder=" " value={values.password} onChange={handleChange} className="inputText" />
                {errors.password && <p className="errorMsg">{errors.password}</p>}
              </div>
            </div>
            <div className="registerSection">
              <div className="regTitle">
                 <span className="star">*</span>
              </div>
              <div className="regInput">
                <input
                  type="password" name="passwordConfirm" placeholder="  " value={values.passwordConfirm} onChange={handleChange} className="inputText"/>
                {errors.passwordConfirm && <p className="errorMsg">{errors.passwordConfirm}</p>}
              </div>
            </div>
            <div className="registerDiv_3">
              <input value={""} type="submit" className="regBtn" />
            </div>
          </form>
        </div>
      )}
    </Formik>
  );
}

export default Register;
